.popup__input-error {
  margin: 0;
  padding: 0;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  color: #FF0000;
  opacity: 0;
  position: absolute;
  top: calc(100% + 3px);
  left: 0;
  transition: opacity 0.3s;
  box-sizing: border-box;
}
  