.element__trash {
  margin: 0;
  padding: 0;
  margin-top: 18px;
  margin-right: 15px;
  border: none;
  background: none;
  background-image:  url('./../../../images/trash.svg');
  background-repeat: no-repeat;
  min-width: 18px;
  min-height: 19px;
  align-self: flex-end;
  position: absolute;
  visibility: hidden;
}
  
.element__trash:hover {
  opacity: 0.5;
  cursor: pointer;
}
  